.container{
  max-width: 1140px;
  margin: auto;
}

.heading{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #590d22;
  box-shadow: 0px 0px 12px #0c0a3e;
  border-radius: 8px;
  margin: 2rem 1rem;
  padding: .7rem 1rem;
  font-weight: 700;
}

.coin-name {
  margin-left: -4rem;
}

/* Coin Item Styles */
.coin-row {
 display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #590d22;
  box-shadow: 0px 0px 12px #000000;
  border-radius: 8px;
  margin: 2rem 1rem;
  padding: .7rem 1rem;
}

.coin-row:hover {
  transform: scale(1.04);
  transition: .3s ease-in-out;
  cursor: pointer;
}

img {
  height: 40px;
  width: auto;
  margin-right: 8px;
}

.img-symbol {
 display: flex;
 align-items: center;
}

@media screen and (max-width: 720px) {
   .hide-mobile {
    display: none;
   }
 }
